<template>
  <div id="login">
    <div class="container">
      <div class="pin">{{pin}}</div>
      <div class="keypad">
        <el-button
          v-for="key in keys"
          :key="key"
          @click="press(key)"
          type="primary" plain>
            {{ key }}
        </el-button>
        <el-button type="danger" plain @click="clear">&larr;</el-button>
        <el-button type="success" plain @click="enter">Ok</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import operator from "../store/user.js"
import Firebase from "../firebase.js"
import Log from "../data/Log.js"

export default {
  name: 'Login',
  components: {
    //Frontend
  },
  data() {
    return {
      pin: '',
      keys: [1, 2, 3, 4, 5, 6, 7, 8, 9, 0],
    }
  },
  methods: {
    press: function(key) {
      this.pin += key;
    },
    clear: function() {
      this.pin = this.pin.substring(0, this.pin.length - 1);
    },
    enter: function() {
      this.userLogin();
    },
    loginFail: function() {
        this.$message({
          message: this.$t('login.wrong_login'),
          type: 'error'
        });
    },
    setOperator: function(user) {
      console.log("setOperator", user);
      this.$message({
        message: 'Welcome ' + user.name,
        type: 'success'
      });
      this.$router.push("/frontend");
    },
    loginSuccess: function (id, data) {
      operator.setUserLogged({
        loggedIn: true,
        id: id,
        name: data.name,
        permissions: data.permissions
      });

      var event = {
        type: Log.EVENT_LOGIN,
        description: operator.name + " " + this.$t("login.loggedin"),
        comment: ''
      }
      new Log(event, 0).writeLog();
      this.setOperator(operator);
    },
    userLogin: function() {
      Firebase.db.collection('operators')
      .where('pin', '==', this.pin)
      .get()
      .then((querySnapshot) => {
        if(querySnapshot.empty) {
          this.loginFail();
        } else {
          querySnapshot.forEach((doc) => {
              console.log(doc.id, " => ", doc.data());
              this.loginSuccess(doc.id, doc.data());
          });
        }
      })
      .catch((error) => {
        console.log("Error getting documents: ", error);
        this.loginFail();
      })
    }
  },
  mounted() {
  },
}
</script>

