  min-height: 20px;
  margin-bottom: 10px;
  border-radius: 12px;
}
.keypad {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: auto;
  grid-gap: 10px;
  padding: 10px;
}
.container {
  padding: 10px;
  width: 30%;
  text-align: center;
  margin: auto;
}
</style>
